.producers {
  background-image: url("../images/bac_steps.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 180px;
  padding-bottom: 285px;
  &__inner {
    margin-top: 75px;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 20px;
    padding-bottom: 20px;
  }
  &__item-img {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    width: 160px;
    height: 135px;
    padding: 10px;
    img {
      width: 100%;
      height: auto;
    }
    & .img5 {
      width: auto;
    }
  }
}
