.slider {
  margin-top: 130px;
  margin-bottom: 130px;
  position: relative;
  &__inner {
    margin: 10px auto 50px;
    max-width: 1230px;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    padding-bottom: 110px;
    position: relative;
  }
  .swiper {
    width: 100%;
    padding-top: 50px;
    padding-bottom: 100px;
  }
  .swiper-slide {
    background-position: center;
    background-size: cover;
    width: 400px;
    height: 400px;
  }
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 5px;
  }
  .swiper-wrapper {
    img {
      // display: block;
      // max-width: 100%;
      // height: auto;
      // margin: 0 auto;
      // object-fit: contain;
    }
  }
  .swiper-button-prev:after,
  .swiper-rtl .swiper-button-next:after,
  .swiper-button-next:after,
  .swiper-rtl .swiper-button-prev:after {
    content: none;
  }
  .swiper-btn {
    position: absolute;
    top: 90%;
    left: 50%;
    right: 50%;
    cursor: pointer;
    img {
      width: 50px;
    }
  }
  .swiper-button-prev {
    transform: translateX(-76px);
  }
  .swiper-button-next {
    transform: translateX(57px);
  }
}
