.services {
  margin-bottom: 215px;
  &__title {
    margin-bottom: 35px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    column-gap: 7px;
    margin-bottom: 7px;
  }
  &__item {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
  }
  &__item-img {
    height: 100%;

    & img {
      display: block;
      max-width: 200%;
      height: auto;
      margin: 0 auto;
      object-fit: contain;
      &#des {
        max-width: 270%;
      }
    }
  }
  &__item-subtitle {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    text-align: center;
    font-family: "nova_bold";
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 141.5%;
    color: #ffffff;
    z-index: 1;
    cursor: pointer;
    // &:hover {
    //   height: 310px;
    //   background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
    //   bottom: 0;
    // }
    &::after {
      content: "";
      width: 100%;
      height: 140px;
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
      border-radius: 5px;
      z-index: -1;
    }
  }
}
