// GENERAL CSS SETTINGS

html {
  scroll-behavior: smooth;
}

body {
  font-family: $userfont;
  font-size: $regular-text;
  line-height: $lineheight;
  color: $text;
  min-width: 320px;
  overflow-x: hidden;
}

.wrapper {
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.main {
  flex: 1 1 auto;
}
.container-big {
  max-width: 1920px;
  margin: 0 auto;
  height: 100%;
}
.container {
  max-width: $containerWidth;
  margin: 0px auto;
  padding: 0px $containerPadding;
  height: 100%;
}

.btn {
  background: linear-gradient(
    260.7deg,
    #58d095 2.73%,
    #58d095 2.74%,
    #169456 111.9%
  );
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 55px;
  padding: 20px 35px;
  color: #fff;
  min-width: 295px;
  &:hover {
    background: $primery;
    transition: $transition;
  }
}

.title {
  font-family: "nova_extrabld";
  font-weight: 800;
  font-size: 50px;
  line-height: 130%;
  text-transform: uppercase;
  color: #232323;
  text-align: center;
}
.hidden-title {
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: end;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  text-align: center;
  border-radius: 15px;
  color: #044d29;
  background-color: #fff;
  border: 1px solid #044d29;
  z-index: 9999;
}

.popup.show {
  display: block;
  animation: fadeOut 5s forwards;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
.content {
  h4 {
    font-size: 32px;
    font-weight: 700;
    padding-bottom: 10px;
    line-height: 1.4;
  }
  p {
    font-size: 18px;
  }
}
