html {
  scroll-behavior: smooth;
}

body {
  font-family: nova_regular, sans-serif;
  font-size: 23px;
  line-height: 1.4;
  color: rgba(0, 0, 0, 0.6);
  min-width: 320px;
  overflow-x: hidden;
}

.wrapper {
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.main {
  flex: 1 1 auto;
}

.container-big {
  max-width: 1920px;
  margin: 0 auto;
  height: 100%;
}

.container {
  max-width: 1620px;
  margin: 0px auto;
  padding: 0px 10px;
  height: 100%;
}

.btn {
  background: linear-gradient(260.7deg, #58d095 2.73%, #58d095 2.74%, #169456 111.9%);
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 55px;
  padding: 20px 35px;
  color: #fff;
  min-width: 295px;
}
.btn:hover {
  background: #4c1b1b;
  transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
}

.title {
  font-family: "nova_extrabld";
  font-weight: 800;
  font-size: 50px;
  line-height: 130%;
  text-transform: uppercase;
  color: #232323;
  text-align: center;
}

.hidden-title {
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: end;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  text-align: center;
  border-radius: 15px;
  color: #044d29;
  background-color: #fff;
  border: 1px solid #044d29;
  z-index: 9999;
}

.popup.show {
  display: block;
  animation: fadeOut 5s forwards;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
.content h4 {
  font-size: 32px;
  font-weight: 700;
  padding-bottom: 10px;
  line-height: 1.4;
}
.content p {
  font-size: 18px;
}

.header {
  background-image: url("../images/bac_header.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 120px;
  padding-top: 28px;
}
.header .menu {
  margin: 0px 10px;
}
.header .social {
  margin-right: 10px;
}
.header__inner {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__number {
  font-family: "nova_bold";
  font-weight: 700;
  font-size: 22px;
  line-height: 130%;
  color: #044d29;
}
.header__number p {
  font-size: 13px;
  line-height: 130%;
  color: #787878;
  position: relative;
  padding-left: 23px;
}
.header__number p::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 7px;
  height: 7px;
  background-color: #044d29;
  border-radius: 50%;
}

.logo {
  display: flex;
  align-items: center;
}

.menu__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 85px;
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
}
.social__list-item {
  background: linear-gradient(180deg, #5ce6a2 0%, rgba(4, 77, 41, 0.66) 100%);
  border: 2px solid #ffffff;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.social__list-item:hover {
  background: #4c1b1b;
  transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
}
.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
}
.social__img {
  padding: 10px;
}

.hero {
  height: calc(100vh - 120px);
  background-image: url("../images/bac-hero.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-bottom: 175px;
  padding-top: 190px;
}
.hero__inner {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.hero__title {
  font-family: "nova_bold";
  font-weight: 700;
  font-size: 63px;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 78px;
}
.hero__title span {
  color: #169456;
}
.hero__btn {
  display: none;
  max-width: 360px;
}

.greet {
  background-image: url("../images/bac_greet.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 80px;
  padding-bottom: 100px;
}
.greet__inner {
  display: flex;
  column-gap: 50px;
}
.greet__img {
  position: relative;
  z-index: 1;
}
.greet__img img {
  max-width: 470px;
}
.greet__img::after {
  content: "";
  position: absolute;
  width: 300px;
  height: 475px;
  left: 0;
  bottom: 73px;
  background: #044d29;
  z-index: -1;
}
.greet__content {
  max-width: 920px;
  padding-top: 100px;
  margin-left: 30px;
  margin-right: 30px;
}
.greet__content-title {
  text-align: left;
}
.greet__content-text {
  margin-top: 30px;
  position: relative;
}
.greet__content-text::after {
  content: "";
  background-color: rgb(88, 208, 149);
  position: absolute;
  width: 2px;
  height: 230px;
  top: 25px;
  left: -25px;
}
.greet__content-text p {
  padding-bottom: 35px;
}
.greet__img {
  width: 470px;
  height: 700px;
}
.greet__content-slogan {
  font-family: "nova_bold";
  font-weight: 700;
  font-size: 21px;
  line-height: 140%;
  color: #044d29;
}

.quality {
  padding-top: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.quality__item {
  text-align: center;
  min-height: 135px;
}
.quality__img {
  width: 118px;
  height: 118px;
  padding-bottom: 6px;
}
.quality__text {
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 140%;
  color: #044d29;
}

.video {
  background-image: url("../images/bac_video.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.video__inner {
  padding-bottom: 175px;
  padding-top: 120px;
  max-height: 885px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.video__content {
  max-height: 470px;
  max-width: 1020px;
  position: relative;
}
.video__content::after {
  position: absolute;
  content: "";
  background-image: url("../images/video_logo.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 118px;
  height: 118px;
}
.video__content img {
  width: 100%;
  height: 100%;
}
.video__title {
  padding: 10px 10px 70px;
}

.departure {
  background-color: #044d29;
  border-radius: 20px;
  color: #fff;
  padding-top: 95px;
  padding-bottom: 95px;
}
.departure .container-big {
  position: relative;
}
.departure .container-big::after {
  content: "";
  width: 950px;
  height: 754px;
  background-image: url("../images/form_img.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  right: 0;
  bottom: -166px;
}
.departure__title {
  text-align: left;
  color: #ffffff;
}
.departure__form-btn {
  max-width: 350px;
  width: 100%;
}
.departure__form-container {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  position: relative;
  z-index: 2;
  padding-bottom: 75px;
}
.departure__form-container input {
  width: 100%;
  max-width: 410px;
  height: 55px;
  border-radius: 10px;
  padding: 10px;
}
.departure__form p {
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 141.5%;
  color: #d9d9d9;
  padding: 35px 0 25px;
}
.departure__img {
  max-width: 810px;
  max-height: 710px;
}
.departure__img img {
  width: 100%;
  height: 100%;
}

.steps {
  background-image: url("../images/bac_steps.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 170px;
  padding-bottom: 180px;
  position: relative;
}
.steps__inner {
  margin: 30px auto;
  max-width: 785px;
}
.steps__list {
  position: relative;
}
.steps__list-num {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  width: 92px;
  height: 92px;
  background: #044d29;
  border-radius: 5px;
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 141.5%;
  color: #ffffff;
}
.steps__list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #ffffff;
  box-shadow: 0px 17px 111px rgba(0, 0, 0, 0.11);
  margin-left: 45px;
  border-radius: 5px;
  padding: 15px 10px 15px 92px;
  margin-bottom: 30px;
  min-height: 114px;
  overflow: hidden;
}
.steps__list-item .sale {
  display: none;
  align-items: center;
  justify-content: center;
  height: 65px;
  background: #f6fa47;
  font-weight: 700;
  font-size: 20px;
  line-height: 141.5%;
  text-transform: uppercase;
  color: #000;
  padding: 0 60px;
  transform: matrix(0.76, -0.65, 0.65, 0.76, 0, 0);
}
.steps__list-title {
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  color: #000000;
}
.steps__list-text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 141.5%;
  color: rgba(0, 0, 0, 0.6);
}

.calc {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(39, 6, 6, 0.7) 100%);
  border-radius: 20px;
  padding-top: 90px;
  padding-bottom: 70px;
  color: #fff;
}
.calc__title {
  color: #fff;
}
.calc .call__subtitle {
  text-align: center;
  padding: 30px 10px;
}
.calc .sale {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 65px;
  background: #f6fa47;
  font-weight: 700;
  font-size: 20px;
  line-height: 141.5%;
  text-transform: uppercase;
  color: #000;
  padding: 0 60px;
}
.calc__form {
  justify-content: center;
  margin: 78px auto 0;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.calc__form label {
  font-style: normal;
  font-weight: 500;
  font-size: 33px;
  line-height: 40px;
  color: #f8f8f8;
}
.calc__form .call__subtitle {
  color: #f8f8f8;
}
.calc__form-inner {
  width: 100%;
  max-width: 580px;
  max-height: 260px;
}
.calc__form-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  flex-wrap: wrap;
}
.calc__form-area, .calc__form-price {
  background: #ffffff;
  text-align: center;
  border-radius: 20px;
  max-width: 60px;
  min-height: 48px;
  padding: 10px;
}
.calc__form-price {
  min-width: 130px;
}
.calc__form-phone {
  background: #ffffff;
  border-radius: 20px;
  max-width: 275px;
  min-height: 48px;
  padding: 10px;
}
.calc__form-btn {
  margin: 25px auto;
}

.producers {
  background-image: url("../images/bac_steps.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 180px;
  padding-bottom: 285px;
}
.producers__inner {
  margin-top: 75px;
}
.producers__item {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 20px;
  padding-bottom: 20px;
}
.producers__item-img {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  width: 160px;
  height: 135px;
  padding: 10px;
}
.producers__item-img img {
  width: 100%;
  height: auto;
}
.producers__item-img .img5 {
  width: auto;
}

.type {
  transform: translateY(-100px);
}
.type__inner {
  margin-top: 35px;
  display: flex;
  justify-content: space-between;
  column-gap: 7px;
}
.type__item {
  position: relative;
}
.type__item::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
}
.type__item-img {
  width: 100%;
  height: 100%;
}
.type__item-subtitle {
  display: flex;
  justify-content: center;
  align-items: end;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
  text-align: center;
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 141.5%;
  color: #ffffff;
  z-index: 2;
  cursor: pointer;
}

.type__item-text,
.services__item-text {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
  overflow: scroll;
  display: flex;
  flex-direction: column;
  justify-content: start;
  background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
  font-size: 16px;
  line-height: 120%;
  color: #fff;
  z-index: 2;
}
.type__item-text p,
.services__item-text p {
  flex: 1 1 auto;
}

.type__item-text h4 {
  font-size: 22px;
  font-weight: 700;
  padding-bottom: 10px;
}

.hidden {
  display: none;
}
.hidden-btn {
  margin-top: 15px;
  border-radius: 20px;
  padding: 10px 25px;
  max-width: 250px;
  color: #fff;
}

.slider {
  margin-top: 130px;
  margin-bottom: 130px;
  position: relative;
}
.slider__inner {
  margin: 10px auto 50px;
  max-width: 1230px;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  padding-bottom: 110px;
  position: relative;
}
.slider .swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 100px;
}
.slider .swiper-slide {
  background-position: center;
  background-size: cover;
  width: 400px;
  height: 400px;
}
.slider .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.slider .swiper-button-prev:after,
.slider .swiper-rtl .swiper-button-next:after,
.slider .swiper-button-next:after,
.slider .swiper-rtl .swiper-button-prev:after {
  content: none;
}
.slider .swiper-btn {
  position: absolute;
  top: 90%;
  left: 50%;
  right: 50%;
  cursor: pointer;
}
.slider .swiper-btn img {
  width: 50px;
}
.slider .swiper-button-prev {
  transform: translateX(-76px);
}
.slider .swiper-button-next {
  transform: translateX(57px);
}

.services {
  margin-bottom: 215px;
}
.services__title {
  margin-bottom: 35px;
}
.services__inner {
  display: flex;
  justify-content: space-between;
  column-gap: 7px;
  margin-bottom: 7px;
}
.services__item {
  position: relative;
  border-radius: 5px;
  overflow: hidden;
}
.services__item-img {
  height: 100%;
}
.services__item-img img {
  display: block;
  max-width: 200%;
  height: auto;
  margin: 0 auto;
  object-fit: contain;
}
.services__item-img img#des {
  max-width: 270%;
}
.services__item-subtitle {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  text-align: center;
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 141.5%;
  color: #ffffff;
  z-index: 1;
  cursor: pointer;
}
.services__item-subtitle::after {
  content: "";
  width: 100%;
  height: 140px;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
  border-radius: 5px;
  z-index: -1;
}

.call {
  background-color: #044d29;
  border-radius: 20px;
  color: #fff;
  text-align: center;
  padding-top: 95px;
  padding-bottom: 95px;
  margin-bottom: 215px;
}
.call__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.call__title {
  color: #fff;
}
.call__subtitle {
  font-size: 30px;
  line-height: 141.5%;
  color: #d9d9d9;
  padding: 20px 0 50px;
}
.call__form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.call__form-container p {
  font-size: 17px;
  line-height: 141.5%;
  color: #d9d9d9;
  padding: 25px 0 35px;
}
.call__form-container input {
  max-width: 410px;
  width: 100%;
  height: 55px;
  border-radius: 10px;
  padding: 17px 20px;
}
.call__form-container input + input {
  margin: 25px 0;
}
.call__content p {
  margin-top: 15px;
}

.footer {
  background: #f1f1f1;
  max-height: 500px;
  padding: 35px 0 35px;
}
.footer__inner {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__number {
  font-family: "nova_bold";
  font-weight: 700;
  font-size: 22px;
  line-height: 130%;
  color: #044d29;
}
.footer__number p {
  font-size: 13px;
  line-height: 130%;
  color: #787878;
  position: relative;
  padding-left: 23px;
}
.footer__number p::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 7px;
  height: 7px;
  background-color: #044d29;
  border-radius: 50%;
}

.menu {
  position: relative;
}

#menu__toggle {
  display: none;
}

.menu__btn {
  display: none;
}

@media (max-width: 1200px) {
  .menu__btn {
    display: flex;
    position: absolute;
    top: 0px;
    right: 0px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #044d29;
  }
  .menu__btn > span {
    background-color: #4c1b1b;
  }
  .menu__btn > span::before {
    content: "";
    top: -8px;
  }
  .menu__btn > span::after {
    content: "";
    top: 8px;
  }
  .menu__list {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 120px 0;
    list-style: none;
    text-align: center;
    background-color: #eceff1;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
    transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
  }
  .menu__list-item {
    display: block;
    padding: 12px 24px;
    color: rgba(0, 0, 0, 0.6);
    font-family: nova_regular, sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }
  .menu__list-item:hover {
    background-color: #cfd8dc;
  }
  #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
  }
  #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
  }
  #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }
  #menu__toggle:checked ~ .menu__list {
    visibility: visible;
    right: 0;
  }
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
  }
  .menu__list {
    transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
  }
  .menu__list-item {
    transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
  }
}
@media (max-width: 1620px) {
  body {
    font-size: 20px;
  }
  .menu__list {
    column-gap: 50px;
  }
  .departure__form-content {
    display: flex;
    flex-direction: column;
    row-gap: 21px;
  }
  .departure__form-content input + input {
    margin-left: 0;
  }
  .departure__form p {
    padding: 30px 0 97px;
  }
  .services,
  .call {
    margin-bottom: 110px;
  }
}
@media (max-width: 1400px) {
