  }
  .greet {
    padding-top: 50px;
    padding-bottom: 70px;
  }
  .greet__inner {
    column-gap: 60px;
  }
  .greet__content-text p {
    padding-bottom: 20px;
  }
}
@media (max-width: 1200px) {
  .header__number {
    z-index: 2;
  }
  .logo {
    z-index: 2;
  }
  .menu {
