  .services__inner {
    flex-direction: column;
    row-gap: 12px;
    margin-bottom: 12px;
  }
}
@media (max-width: 768px) {
  .header .social {
    display: none;
  }
  .hero__title {
    font-size: 48px;
  }
  .greet__inner {
    flex-direction: column;
  }
  .quality {
    display: flex;
  }
  .greet__content-text::after {
    height: 190px;
  }
  .greet__content {
    padding-top: 0px;
  }
  .quality {
    padding-top: 25px;
  }
  .services {
    margin-bottom: 150px;
  }
  .steps__list-item .sale {
