    bottom: 0;
    left: 50%;
    right: auto;
    transform: translate(-50%, 50%);
  }
  .calc__form-btn {
    bottom: -23px;
  }
  .title {
    font-size: 30px;
  }
  .steps__list-num {
    left: 7px;
  }
  .steps__list-item {
    position: relative;
    margin-left: 0;
    padding-left: 112px;
  }
  .steps__list-item .steps__list-content {
    position: absolute;
  }
  .steps__list-item .sale {
    margin-left: 65px;
  }
  .calc,
  .call {
    padding-bottom: 70px;
    position: relative;
  }
  .calc {
    padding-top: 45px;
  }
  .calc__form-phone,
  .calc__form-price,
  .calc__form-area {
    min-height: 42px;
  }
  .calc__form label {
