  }
  .producers,
  .steps {
    padding-top: 140px;
    padding-bottom: 80px;
  }
  .type {
    transform: none;
    padding-top: 50px;
  }
  .type__inner,
  .services__inner {
    margin-top: 16px;
  }
  .type__item-subtitle,
  .services__item-subtitle {
    font-size: 17px;
  }
  .slider {
    margin-top: 70px;
    margin-bottom: 110px;
  }
  .slider__inner {
    margin: 20px auto;
    padding-bottom: 70px;
  }
  .slider .swiper-slide {
    width: 300px;
    height: 300px;
  }
  .slider .swiper-btn img {
    width: 35px;
  }
  .call__form-content input {
    width: 350px;
  }
  .services {
    margin-bottom: 100px;
  }
  .call__subtitle {
    font-size: 17px;
  }
  .call {
    margin-bottom: 100px;
  }
  .footer .footer__number {
    display: none;
  }
}