.steps {
  background-image: url("../images/bac_steps.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 170px;
  padding-bottom: 180px;
  position: relative;
  &__inner {
    margin: 30px auto;
    max-width: 785px;
  }
  &__list {
    position: relative;
  }
  &__list-num {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    width: 92px;
    height: 92px;
    background: #044d29;
    border-radius: 5px;
    font-family: "nova_bold";
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 141.5%;
    color: #ffffff;
  }
  &__list-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #ffffff;
    box-shadow: 0px 17px 111px rgba(0, 0, 0, 0.11);
    margin-left: 45px;
    border-radius: 5px;
    padding: 15px 10px 15px 92px;
    margin-bottom: 30px;
    min-height: 114px;
    overflow: hidden;
    .sale {
      display: none;
      align-items: center;
      justify-content: center;
      height: 65px;
      background: #f6fa47;
      font-weight: 700;
      font-size: 20px;
      line-height: 141.5%;
      text-transform: uppercase;
      color: #000;
      padding: 0 60px;
      transform: matrix(0.76, -0.65, 0.65, 0.76, 0, 0);
    }
  }
  &__list-title {
    font-family: "nova_bold";
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    color: #000000;
  }
  &__list-text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 141.5%;
    color: #00000099;
  }
}
