.calc {
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.7) 0%,
    rgba(39, 6, 6, 0.7) 100%
  );
  border-radius: 20px;
  padding-top: 90px;
  padding-bottom: 70px;
  color: #fff;
  &__title {
    color: #fff;
  }
  .call__subtitle {
    text-align: center;
    padding: 30px 10px;
  }
  .sale {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 65px;
    background: #f6fa47;
    font-weight: 700;
    font-size: 20px;
    line-height: 141.5%;
    text-transform: uppercase;
    color: #000;
    padding: 0 60px;
  }

  &__form {
    justify-content: center;
    margin: 78px auto 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    & label {
      font-style: normal;
      font-weight: 500;
      font-size: 33px;
      line-height: 40px;
      color: #f8f8f8;
    }
    .call__subtitle {
      color: #f8f8f8;
    }
  }
  &__form-inner {
    width: 100%;
    max-width: 580px;
    max-height: 260px;
  }
  &__form-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    flex-wrap: wrap;
  }
  &__form-area,
  &__form-price {
    background: #ffffff;
    text-align: center;
    border-radius: 20px;
    max-width: 60px;
    min-height: 48px;
    padding: 10px;
  }
  &__form-price {
    min-width: 130px;
  }
  &__form-phone {
    background: #ffffff;
    border-radius: 20px;
    max-width: 275px;
    min-height: 48px;
    padding: 10px;
  }
  &__form-btn {
    margin: 25px auto;
  }
}
