.type {
  transform: translateY(-100px);
  &__inner {
    margin-top: 35px;
    display: flex;
    justify-content: space-between;
    column-gap: 7px;
  }
  &__item {
    position: relative;
    &::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
    }
  }
  &__item-img {
    width: 100%;
    height: 100%;
  }
  &__item-subtitle {
    display: flex;
    justify-content: center;
    align-items: end;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;
    text-align: center;
    font-family: "nova_bold";
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 141.5%;
    color: #ffffff;
    z-index: 2;
    cursor: pointer;
  }
}
.type__item-text,
.services__item-text {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
  overflow: scroll;
  display: flex;
  flex-direction: column;
  justify-content: start;
  background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
  font-size: 16px;
  line-height: 120%;
  color: #fff;
  z-index: 2;
  & p {
    flex: 1 1 auto;
  }
}
.type__item-text {
  & h4 {
    font-size: 22px;
    font-weight: 700;
    padding-bottom: 10px;
  }
}
.hidden {
  display: none;
  &-btn {
    margin-top: 15px;
    border-radius: 20px;
    padding: 10px 25px;
    max-width: 250px;
    color: #fff;
  }
}
